@media screen and (max-width: 720px) {
    * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        text-align: center;
    }

    h1 {
        font-size: 2rem;
        font-weight: 600;
    }

    .alpha-wrapper {
        width: 100%;
        padding: 0;
    }

    .burger-div {
        display: flex;
        flex-direction: column;
        gap: 5px;
        width: 3rem;
        height: 3rem;
        position: absolute;
        top: 2rem;
        right: 2rem;
        cursor: pointer;

        div {
            width: 100%;
            height: 0.2rem;
            background: rgb(228, 228, 228);
        }
    }

    header {
        height: 90vh;
        padding: 1rem;
        transition: all 0.3s ease;
        nav {
            flex-direction: column;
            padding: 2rem;
            gap: 3rem;
            position: relative;
        }

        img {
            display: block;
            margin-left: -10rem;
        }

        .wrapper {
            background: white;
            opacity: 0;
            //display: none;
            display: flex;

            flex-direction: column;
            width: 5rem;
            //height: 80vh;
            height: 0vh;
            width: 100%;
            border-radius: 10px;

            ul {
                display: flex;
                flex-direction: column;
                color: black;
            }

            .last-div {
                display: flex;
                flex-direction: column;
            }

            .sign-up {
                width: 8rem;
                background: linear-gradient(45deg, #ff8e70, #ff4359);
                color: white;
                //comment
            }

            .login {
                width: 8rem;
                height: 2rem;
                color: black;
            }
        }
    }

    .section-0 {
        h1 {
            margin: 0.2rem;
        }

        p {
            font-size: 1.2rem;
            font-weight: 300;
